.header{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack:justify;
        justify-content:space-between;
    padding:20px;
    background-color: white;
    top:0;
    position: sticky;
    z-index: 100;

}
/*
.header.shadow { 
    -webkit-box-shadow: 0 3px 5px rgba(57, 63, 72, 0.3);
  -moz-box-shadow: 0 3px 5px rgba(57, 63, 72, 0.3);
  box-shadow: 0 3px 5px rgba(57, 63, 72, 0.3);
}*/

.header__left{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
   
}
.header__right{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}
.header__icon{
    margin-right:5px;
}

.header__logo{
    margin-left: 20px;
    height:52px ;
    -o-object-fit: contain;
       object-fit: contain;
   

}
.header__input{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    width: 60%;
    border: 1px solid lightgrey;   
}
.header__input > input{
    -ms-flex: 1 1;
        flex: 1 1;
    border:none;
    height:40px;
    font-size:20px;
    padding-left:10px;
    padding-right:10px;

}
.header__input > input:focus{
    outline: none !important;
    border:none;
   

}

.header__input_button{
   width:50px !important;
   background-color:#fafafa;
   border-left: 1px solid lightgrey;
   color:grey;
}

.header__text > label{
    font-size:24px;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: -1.5px;
}
.header__text > label >span{
    color: red;
}

.header__right > button{
    color:white;
    text-transform: uppercase;
    background-color: red;
    padding: 10px;
    border:white;
    margin-left:10px;
    margin-right:10px;
    
}
.header__right > label{
    color:grey;
    margin-right:10px;
    
}

.header__right > label:focus{
    text-decoration: underline;   
}



