.recomendedvideo{
    
 
   -ms-flex:0.8 1;
    
 
       flex:0.8 1;
   background:#f9f9f9;
   padding:15px;
}
.customClassName{
   
    border:0px solid whitesmoke;
    margin-bottom:30px;
    width:200px !important;
    margin-right:5px;
    border-radius: 5px 5px;
}
.customClassName > div > video{
    border-radius:5px 5px !important;
    height: 150px !important;
    width:200px !important;
    background-color: black;
}


.recomendedvideo__header  > h1 > .recomendedvideo__Title{
   width:100%;
   text-transform: uppercase;
   display: -ms-flexbox;
   display: flex;
   font-size: 14px;
   -ms-flex-align: center;
       align-items: center;
   font-size: 18px;
   color:indianred;
   padding:10px;
 
  
}

.recomendedvideo__header > h1{
    -ms-flex-pack: justify;
        justify-content: space-between;
    display: -ms-flexbox;
    display: flex;
    margin-bottom:10px;
}
.recomendedvideo__header > h1>  .recomendedvideo__Title {
    display: -ms-flexbox;
    display: flex;
    width:30%;
}
.recomendedvideo__header > h1>  .recomendedvideo__Title > span{
   margin-left:16px;
  
   -webkit-transform: scale(1.2, 1.2);
  
       -ms-transform: scale(1.2, 1.2);
  
           transform: scale(1.2, 1.2);
 }
 
  .recomendedvideo__header  > .recomendedvideo__Label{
    margin-left:15px;
    font-size:16px;
    -webkit-transform: scale(1.2, 1.2);
        -ms-transform: scale(1.2, 1.2);
            transform: scale(1.2, 1.2);
    border:1px solid white;
    padding:10px;
  }
.recomendedvideo__header > hr{
    color:grey;
   
        display: block;
        
        width: 100%;
        height: 1px;
        border: 0;
        margin-bottom:40px;
        background-color: rgba(216, 206, 206, 0.35);
     }

 
 

.recomendedvideo__body{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-line-pack: center;
        align-content: center;

    padding:10px;
    margin-bottom:10px;
}




