.sidebarRow{
 
    -ms-flex-align: center;
 
        align-items: center;
    display: -ms-flexbox;
    display: flex;
    padding:10px 20px;
    
}
.sidebarRow_icon{
    color:#606060;
    font-size: large !important;
  
}

.sidebarRow_title{
    -ms-flex:1 1;
        flex:1 1;
    margin-left:20px;
    font-size:16px;
    font-weight: 400;
}

.sidebarRow:hover{
    background-color: lightgrey;
}
.sidebarRow:hover> .sidebarRow_icon{
    color:red;
}
.sidebarRow:hover> .sidebarRow_title{
    font-weight:bold;
}

.sidebarRow.selected {
    background-color: lightgrey;
}
.sidebarRow.selected > .sidebarRow_icon {
    color:red;
}
.sidebarRow.selected > .sidebarRow_title {
    font-weight:bold;
}


